export default {
  name: "classroomTesting",
  data() {
    return {};
  },
  //   直接执行
  mounted() {
    this.setBodyEle();
  },
  //   方法
  methods: {
    // 公用的选择方法
    selectRankingAll: function(item, index, ListName) {
      const _this = this;
      _this.option[ListName].map(function(event, idx) {
        if (event.name == item.name) {
          event.type = true;
          _this.$set(_this.option[ListName], idx, event);
        } else {
          event.type = false;
          _this.$set(_this.option[ListName], idx, event);
        }
      });
    },
    setBodyEle: function() {
      let _body = document.querySelector("body");
      let _html = document.querySelector("html");
      let _app = document.getElementById("app");

      //   _body.style.overflow = "hidden";
      //   _html.style.overflow = "hidden";
      _app.style.height = 100 + "%";
      _html.style.height = 100 + "%";
      _body.style.height = 100 + "%";
    }
  }
};



